.img-1 {
  animation-name: swap;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes swap {
  0%, 20% {
    opacity: 1;
  }

  30%, 40% {
    opacity: 0;
  }

  60%, 70% {
    opacity: 0;
  }

  80%, 100% {
    opacity: 1;
  }
}

